<template>
    <v-data-table :headers="headers" :items="minum" :search="search" sort-by="minuman" class="elevation-1">
        <template v-slot:top>
            <v-toolbar flat color="white">
                <v-toolbar-title>Crud Atma pedia</v-toolbar-title>
                <v-divider class="mx-4" inset vertical></v-divider>
                <v-spacer></v-spacer>
                <v-spacer></v-spacer>
                <v-btn color="primary" dark class="mb-2 mr-3" @click="deleteItemMultiple()">Delete Banyak</v-btn>
                <v-dialog v-model="dialog" max-width="500px">
                    <template v-slot:activator="{ on }">
                        <v-btn color="primary" dark class="mb-2" v-on="on">buat minuman</v-btn>
                    </template>
                    <v-card>
                        <v-card-title>
                            <span class="headline">{{ formTitle }}</span>
                        </v-card-title>
                        <v-card-text>
                            <v-container>
                                <v-row>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.minuman" label="Nama Minuman"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-select v-model="editedItem.jenis" :items="jenisMinuman" label="Jenis"></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-select v-model="editedItem.kondisi" :items="kondisiMinuman" label="Kondisi"></v-select>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.gula" label="Kandungan Gula (ml)"></v-text-field>
                                    </v-col>
                                    <v-col cols="12" sm="6" md="4">
                                        <v-text-field v-model="editedItem.asam" label="Kandungan Asam (ml)"></v-text-field>
                                    </v-col>
                                </v-row>
                            </v-container>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="blue darken-1" text @click="close">Cancel</v-btn>
                            <v-btn color="blue darken-1" text @click="save">Save</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-toolbar>
        </template>
                <template v-slot:item.multi="{ item }">
                </template>
                <template v-slot:item.aksi="{ item }">
                    <v-icon class="mr-1" @click="edit(item)">mdi-silverware</v-icon>
                    <v-icon class="mr-1" @click="deleteItem(item)">mdi-delete</v-icon>
                </template>
    </v-data-table>
</template>
<script>
    import {minumanRef} from '../firebase';
    export default {
        name: 'Minuman',
        data: () => ({
            search: '',
            cek: -1,
            dialog: false,
            headers: [{
                    text: 'Minuman',
                    align: 'left',
                    sortable: true,
                    value: 'minuman',
                },
                {
                    text: 'Jenis',
                    value: 'jenis'
                },
                {
                    text: 'Kondisi',
                    value: 'kondisi'
                },
                {
                    text: 'Kandungan Gula (ml)',
                    value: 'gula'
                },
                {
                    text: 'Kandungan Asam (ml)',
                    value: 'asam'
                },
                {
                    text: 'Aksi',
                    value: 'aksi',
                    sortable: false
                },
            ],
            minum: [],
            jenisMinuman: ['Tradisional','Campuran','Manis','Pahit'],
            kondisiMinuman: ['Panas','Sedang','Dingin'],
            editedIndex: -1,
            editedItem: {
                minuman: '',
                jenis: '',
                kondisi: '',
                gula: 0,
                asam: 0,
            },
            defaultItem: {
                minuman: '',
                jenis: '',
                kondisi: '',
                gula: 0,
                asam: 0,
            },
        }),
        firebase: {
          minum : minumanRef
        },
        computed: {
            formTitle() {
                return this.cek === -1 ? 'New Item' : 'Edit Item'
            },
        },
        watch: {
            dialog(val) {
                val || this.close()
            },
        },
        methods: {
            close() {
                this.dialog = false
                setTimeout(() => {
                    this.editedItem = Object.assign({}, this.defaultItem)
                    this.editedIndex = -1
                }, 300)
            },
            // methods untuk save
            save() {
            if (this.cek > -1) { 
              minumanRef.child(this.editedIndex).set({minuman: this.editedItem.minuman,jenis:
                this.editedItem.jenis , kondisi : this.editedItem.kondisi, gula: this.editedItem.gula, asam: this.
                editedItem.asam}) 
              this.clear() 
            } else { 
                minumanRef.push({minuman: this.editedItem.minuman,jenis:
                this.editedItem.jenis , kondisi : this.editedItem.kondisi, gula: this.editedItem.gula, asam: this.
                editedItem.asam}) 
                    this.clear() 
              } this.close()
            },
            edit(item) {
                this.editedIndex = item['.key']
                this.cek = 0
                this.editedItem = Object.assign({},item)
                this.dialog = true
            },
            deleteItem(item) {
                confirm('Are you sure you want to delete this item?') && minumanRef.child((item['.key'])).remove()
            },
            deleteItemMultiple(){
                for(this.empty=0;this.empty<this.checkHapus.length;this.empty++){

                    minumanRef.child(this.checkHapus[this.empty]).remove()
                }
                this.checkHapus=[]
            },
            clear(){
              this.editedItem={}
              this.editedItem.minuman=''
              this.editedItem.jenis=''
              this.editedItem.kondisi=''
              this.editedItem.gula=0
              this.editedItem.asam=0
              this.cek=-1
            }
        },
    }
</script>